import React, { useRef } from "react";
import Layout from "./Layout";
import PPT from "pptxgenjs";

const styleSheet = {
  container: {
    background: "black",
    height: "1000px",
    color: "white",
    textAlign: "center",
    paddingTop: "30px",
    fontSize: "4em"
  }
};
const View = () => {
  const ref = useRef("");
  const download = () => {
    const pptx = new PPT();
    console.log(ref.current);
    pptx.addSlidesForTable();
  };
  return (
    <Layout title="View">
      <div style={styleSheet.container} ref={ref}>
        View
      </div>
      <button onClick={download}>Hello</button>
    </Layout>
  );
};

export default View;
